@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
  height: 100vh;
  width: 100%;
  background-color: #292D32;
  display: grid;
  grid-template-rows: 90px 60px 1fr;
  grid-template-columns: 1fr;
  grid-template-areas:
  "h"
  "n"
  "c";
}

.app-wrapper-content {
  position: relative;
  width: 900px;
  height: 70vh;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 5vh;
  grid-area: c;
  border-radius: 30px;
  box-shadow: 18px 18px 20px #2A2D32, -18px -5px 20px #30343A;
  font-family: 'Montserrat', sans-serif;
  background-color: white;
}
.nav {
    grid-area: n;
    width: 900px;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas:
"ne di . us pr";
}
.h_nav a {
    color: darkgray;
    text-decoration: none;
}
.h_nav a.active {
    color: white;
}
.h_nav {
    display: inline-block;
    margin: auto;
    font-family: "Arial Hebrew Scholar", Arial, sans-serif;
    font-weight: bold;
    font-size: 16px;
}
.pr {grid-area: pr;}
.ne {grid-area: ne;}
.di {grid-area: di;}
.us {grid-area: us;}
.yinYang {
    position: absolute;
    display: block;
    left: 50%;
    margin: -34px 0 0 -76px;
    width: 150px;
    height: 75px;
    border: solid #292D32;
    border-width: 3px 3px 75px 3px;
    border-radius: 100%;
    background: whitesmoke;
    z-index: revert;
}
.yinYang:before {
    position: absolute;
    top: 50%;
    left: 0;
    width: 18px;
    height: 18px;
    content: '';
    background: whitesmoke;
    border: 29px solid #292D32;
    border-radius: 100%;
}
.yinYang:after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 18px;
    content: '';
    background: #292D32;
    border: 29px solid whitesmoke;
    border-radius: 100%;
}
.header {
    grid-area: h;
}
.header {
    position: relative;
    width: 900px;
    margin: auto;
    text-align: center;
    font-family: "Arial Black", Arial, sans-serif;
    color: whitesmoke;
    font-size: 40px;
}
.header__PRO {
    position: relative;
    display: inline-block;
    top: -6px;
    margin-left: 10px;
    padding: 0 10px;
    border-radius: 100px;
    font-size: 22px;
    color: #292D32;
    background-color: whitesmoke;
}
.loginBlock {
    position: absolute;
    display: block;
    width: inherit;
    top: 18px;
    margin-left: auto;
    margin-right: auto;
    text-align: right;
    font-size: 16px;
}
.loginBlock a {
    text-decoration: none;
    color: lightgray;
}
@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
.yin-yang {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    margin: -25px 0 0 -50px;
    width: 100px;
    height: 50px;
    border: solid #292D32;
    border-width: 2px 2px 50px 2px;
    border-radius: 100%;
    background: whitesmoke;
    z-index: 1;
    animation: spin 8s linear infinite;
}
.yin-yang:before {
    position: fixed;
    top: 50%;
    left: 0;
    width: 12px;
    height: 12px;
    content: '';
    background: whitesmoke;
    border: 19px solid #292D32;
    border-radius: 100%;
}
.yin-yang:after {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 12px;
    height: 12px;
    content: '';
    background: #292D32;
    border: 19px solid whitesmoke;
    border-radius: 100%;
}
.postsContainer {
    padding: 20px;
}
.post {
    display: grid;
    width: 600px;
    margin: 0.6em 0;
    padding: 0.3em;
    border-radius: 15px;
    box-shadow: 0 0 15px #E6F0F1;
    grid-template-rows: 1rem 1fr 1rem;
    grid-template-columns: 50px 1fr;
    grid-template-areas:
            'a n'
            'a m'
            'a l';
}
.postAvatar {
    grid-area: a;
}
.postName {
    grid-area: n;
    font-weight: 600;
}
.postMessage {
    grid-area: m;
    color: #30343A;
}
.postLike {
    grid-area: l;
    text-align: right;
    color: darkgrey;
    font-size: 0.8rem;
    font-weight: 600;
}
.avatar {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: grey;
}
* {
    border: none;
    font-family: inherit;
}
.field {
    display: inline-block;
    margin: 0.2rem;
    padding: 3px 10px;
    border-radius: 50px;
    background-color: #E6F0F1;
}
button {
    height: 90%;
    border-radius: 50px;
    font-weight: 600;
    padding: 3px 15px;
}
.paginator {
    display: grid;
    margin-bottom: 10px;
    text-align: center;
    grid-auto-columns: 1fr 1fr 4fr 1fr 1fr;
    grid-template-areas: '. prev page next .';
}
.page {
    grid-area: page;
    font-size: 0.7rem;
}
.selectedPage {
    font-weight: bold;
    color: #30343A;
}
.noSelectedPage {
    color: gray;
}
.prevButton {
    grid-area: prev;
}
.nextButton {
    grid-area: next;
}
.userSearch {
    display: grid;
    padding-top: 5px;
    grid-auto-columns: 1fr 2fr 3fr 1fr 1fr;
    grid-template-areas: '. friend term button .';
}
.term {
    grid-area: term;
}
.friend {
    grid-area: friend;
}
.userButton {
    grid-area: button;
}
.usersContainer{
    text-align: center;
}
.userBlock{
    display: inline-block;
    position: relative;
    width: 200px;
    height: 95px;
    margin: 0.6em 0.5em;
    border-radius: 15px;
    box-shadow: 0 0 15px #E6F0F1;
}
.photoUser {
    width: 50px;
    height: 50px;
    border: solid 3px white;
    border-radius: 50px;
}
.photoUser-Users {
    position: absolute;
    top: -20px;
    left: 75px;
}
.userName {
    position: absolute;
    top: 37px;
    width: 100%;
    text-align: center;
    font-weight: 600;
    overflow: hidden;
}
.userStatus {
    position: absolute;
    top: 56px;
    width: 100%;
    height: 1rem;
    text-align: center;
    font-size: 0.7rem;
    overflow: hidden;
}
.usersButton {
    position: absolute;
    left: 50px;
    bottom: 5px;
    width: 100px;
    height: 18px;
    padding-bottom: 5px;
    border: none;
    border-radius: 7px;
    font-family: inherit;
    font-size: 0.7rem;
    font-weight: 700;
}
.followButton{
    color: whitesmoke;
    background-color: royalblue;
}
